* {
  margin: 0;
  padding: 0;
  font-family: "Avenir Next", "Avenir", sans-serif;
}

body {
  background: url(./nutri-background.jpg) no-repeat center center fixed;
  background-size: cover;
  -moz-background-size: cover;
  -webkit-background-size: cover;
  -o-background-size: cover;
  background-attachment: fixed;
}

.App {
  padding: 20px;
}

h1 {
  color: black;
  width: 100%;
}

p {
  color: black;
}

.container-dashboard {
  margin-bottom: 20%;
  width: 90%;
}

.container {
  justify-content: center;
  display: flex;
  flex-direction: row;
  margin-bottom: 7%;
}

.container-login-signup {
  width: 70%;
  height: 307px;
  margin-top: 80px;
  margin-left: 30px;
  margin-right: 30px;
  margin-bottom: 22%;
  border: 1px solid white;
  background-color: rgba(255, 255, 255, 0.9);
  text-align: center;
  align-content: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.container-userform {
  height: auto;
  width: 70%;
  margin-top: 80px;
  margin-left: 30px;
  margin-right: 30px;
  margin-bottom: 22%;
  border: 1px solid white;
  background-color: rgba(255, 255, 255, 0.9);
  text-align: center;
  align-content: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.container-form {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}

.container-profile {
  width: 80%;
  height: 500px;
  margin-top: 20%;
  margin-bottom: 22%;
  border: 1px solid white;
  background-color: rgba(255, 255, 255, 0.9);
  text-align: center;
  display: flex;
  flex-direction: column;
}

.container-profile img {
  float: left;
}

.container-dashboard img {
  width: 90px;
}

.form-login-signup input,
.userform input {
  margin-top: 13px;
  width: 60%;
}

select {
  width: 60%;
}
.form-login-signup form,
.userform form {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.form-login-signup .login-btn,
.userform .login-btn {
  border: none;
  color: white;
  padding: 9px 5px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  flex-direction: row;
  font-size: 16px;
  background-color: #b6ce3f;
  transition-duration: 0.4s;
  border-radius: 12px;
  width: 43%;
}

.form-login-signup p,
.userform p {
  width: 100%;
}

.userform .login-btn {
  border: none;
  color: white;
  padding: 9px 5px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  flex-direction: row;
  font-size: 16px;
  background-color: #b6ce3f;
  transition-duration: 0.4s;
  border-radius: 12px;
  width: 43%;
}

.container-users {
  margin-top: 30px;
  border: 2px solid white;
  background-color: rgba(255, 255, 255, 0.9);
  display: flex;
  justify-content: center;
  flex-direction: row;
  flex-wrap: wrap;
}

.container-users a {
  width: 50%;
}

.user-card p {
  color: black;
  width: 100%;
  text-align: center;
  margin-top: 10%;
}

.user-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 15px;
}

.imgUser {
  width: 120px;
}

.profile-info {
  width: 50%;
  float: right;
}

.container-profile {
}

.top-part {
  flex-direction: row;
}

.bottom-part {
  text-align: start;
  margin-left: 15px;
}

.btn-diet {
  margin: 10%;
  text-align: start;
  font-size: 20px;
  list-style: none;
}

.profile-info {
  text-align: start;
  margin-top: 20px;
}

.profileImg img {
  width: 115px;
}

.profileName {
  font-size: 16px;
}
